html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--off-white);
}
body.noscroll {
  overflow: hidden;
}

img {
  margin: 0;
  vertical-align: middle;
}

picture {
  margin: 0;
}
picture img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

p {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

input:focus {
  outline: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ai-b {
  align-items: baseline;
}
.flex-d-c {
  flex-direction: column;
}
.flex-d-r {
  flex-direction: row;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.attribution {
  font-size: 15px;
  text-align: center;
  margin-bottom: 15px;
  color: var(--dark-grayish-blue);
}

.attribution a {
  color: hsl(228deg, 45%, 44%);
  font-weight: 700;
}

:root {
  --soft-orange: hsl(35, 77%, 62%);
  --soft-red: hsl(5, 85%, 63%);
  --off-white: hsl(36, 100%, 99%);
  --grayish-blue: hsl(233, 8%, 79%);
  --dark-grayish-blue: hsl(236, 13%, 42%);
  --very-dark-blue: hsl(240, 100%, 5%);
}

@font-face {
  font-family: InterX;
  src: url("/assets/fonts/static/Inter-Bold.ttf") format("ttf"), url("/assets/fonts/static/Inter-ExtraBold.ttf") format("ttf"), url("/assets/fonts/static/Inter-Regular.ttf") format("ttf");
}
:root {
  --fs-base: 1rem;
  --fs-scale-ratio: .8;
  --fs-xxlg: calc(var(--fs-base) * var(--fs-scale-ratio) * 3.375);
  --fs-xlg: calc(var(--fs-base) * var(--fs-scale-ratio) * 2.25);
  --fs-lg: calc(var(--fs-base) * var(--fs-scale-ratio) * 2.25);
  --fs-md: calc(var(--fs-base) * var(--fs-scale-ratio) * 1.1875);
  --fs-sm: calc(var(--fs-base) * var(--fs-scale-ratio) * 1);
  --fs-xs: calc(var(--fs-base) * var(--fs-scale-ratio) * 0.9375);
}

@media (min-width: 36em) {
  :root {
    --fs-scale-ratio: .85;
  }
}
@media (min-width: 48em) {
  :root {
    --fs-scale-ratio: .9;
  }
}
@media (min-width: 64em) {
  :root {
    --fs-scale-ratio: 1;
  }
}
@media (min-width: 87.5em) {
  :root {
    --fs-scale-ratio: 1.1;
  }
}
body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 0.9375rem;
  color: var(--grayish-blue);
}

p {
  line-height: 1.8;
}

h1 {
  font-size: var(--fs-xxlg);
  font-weight: 900;
  line-height: 1.3;
}

h2 {
  font-size: var(--fs-xlg);
  font-weight: 700;
}

h3 {
  font-size: 1rem;
}

h4 {
  font-size: 0.9375rem;
}

.container {
  max-width: 90%;
  margin: 0 auto;
}
@media (min-width: 87.5em) {
  .container {
    max-width: 85rem;
  }
}

.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: var(--dark-grayish-blue);
  z-index: 1;
  transition: all 300ms ease-in-out;
}
.overlay.show {
  visibility: visible;
  opacity: 0.55;
}
.overlay.hide {
  visibility: hidden;
  opacity: 0;
}

.header__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.6875rem;
  padding-bottom: 2rem;
}
@media (min-width: 48em) {
  .header__wrapper {
    padding-top: 4.375rem;
    padding-bottom: 3.125rem;
  }
}
@media (max-width: 47.9375em) {
  .header__logo__img {
    width: 50px;
    height: 30px;
  }
}
.header__hamburger {
  z-index: 2;
}
.header__hamburger > span {
  display: block;
  width: 40px;
  height: 3px;
  background-color: var(--very-dark-blue);
  transition: all 200ms ease-in-out;
  transform-origin: 3px 1px;
}
.header__hamburger > span:not(:last-child) {
  margin-bottom: 4px;
}
.header__hamburger.open {
  z-index: 1000;
}
.header__hamburger.open > span:first-child {
  transform: translateY(19px) translateX(0) rotate(-45deg);
}
.header__hamburger.open > span:nth-child(2) {
  opacity: 0;
}
.header__hamburger.open > span:last-child {
  transform: translateY(-19px) translateX(0) rotate(45deg);
}

.desktop_nav ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
}
.desktop_nav ul li {
  transition: all 200ms ease-in-out;
}
.desktop_nav ul li:not(:last-child) {
  padding-right: 2.1875rem;
}
.desktop_nav ul li:hover {
  transform: scale(1.1);
}
.desktop_nav ul li:hover a {
  color: var(--soft-red);
}
.desktop_nav ul li a {
  color: var(--dark-grayish-blue);
}

@keyframes slide-in {
  from {
    visibility: hidden;
    opacity: 0;
    width: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
    width: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
    width: 65%;
  }
}
@keyframes slide-out {
  from {
    visibility: visible;
    opacity: 1;
    width: 65%;
  }
  99% {
    visibility: visible;
    opacity: 0;
    width: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
    width: 0;
  }
}
.slide-in {
  animation: slide-in 300ms ease-in-out forwards;
}

.slide-out {
  animation: slide-out 300ms ease-in-out backwards;
}

.side-menu {
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0;
  width: 65%;
  margin-left: auto;
  padding-left: 1.5625rem;
  background-color: var(--off-white);
  z-index: 1000;
  height: 100vh;
}
.side-menu .mobile_nav ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 9.375rem;
}
.side-menu .mobile_nav ul li:not(:last-child) {
  padding-bottom: 2rem;
}
.side-menu .mobile_nav ul li a {
  color: var(--very-dark-blue);
}

.top {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
}
@media (min-width: 48em) {
  .top {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr;
    gap: 1.875rem;
    margin-bottom: 4.5rem;
  }
}
.top .feature {
  display: grid;
  grid-template-areas: "f_image" "f_title" "f_content";
  grid-template-columns: auto;
  grid-template-rows: repeat(3, auto);
  margin-bottom: 2.75rem;
}
@media (min-width: 48em) {
  .top .feature {
    grid-template-areas: "f_image f_image" "f_title" "f_content";
    grid-template-rows: auto auto auto;
    grid-template-columns: auto;
    margin-bottom: 0;
  }
}
@media (min-width: 64em) {
  .top .feature {
    grid-template-areas: "f_image f_image" "f_title f_content";
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 0;
  }
}
@media (min-width: 75em) {
  .top .feature {
    grid-template-areas: "f_image f_image" "f_title f_content";
    grid-template-rows: 2fr 1.5fr;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 0;
  }
}
.top .feature__image {
  grid-area: f_image;
  margin-bottom: 1.75rem;
}
@media (min-width: 48em) {
  .top .feature__image {
    margin-bottom: 0;
  }
}
.top .feature__title {
  grid-area: f_title;
  color: var(--very-dark-blue);
  margin-bottom: 1.75rem;
  padding-right: 0.625rem;
}
@media (min-width: 48em) {
  .top .feature__title {
    margin-bottom: 0;
  }
}
.top .feature__content {
  grid-area: f_content;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 1.75rem;
}
@media (min-width: 48em) {
  .top .feature__content {
    margin-bottom: 0;
  }
}
@media (min-width: 64em) {
  .top .feature__content {
    justify-content: center;
  }
}
.top .feature__content__excerpt {
  padding-right: 1.875rem;
  color: var(--dark-grayish-blue);
  margin-bottom: 2.375rem;
}
@media (min-width: 48em) {
  .top .feature__content__excerpt {
    margin-bottom: 0;
  }
}
@media (min-width: 64em) {
  .top .feature__content__excerpt {
    margin-bottom: 2.375rem;
  }
}
.top .feature__content__btn {
  position: relative;
  cursor: pointer;
  text-transform: uppercase;
  background-color: var(--soft-red);
  color: var(--off-white);
  display: flex;
  width: 11.25rem;
  height: 3.125rem;
  transition: all 0.6s ease-in-out;
  letter-spacing: 4px;
  font-size: 0.6875rem;
  font-weight: 700;
  overflow: hidden;
  justify-content: center;
  align-items: center;
}
.top .feature__content__btn:focus {
  outline: 0;
}
.top .feature__content__btn:before {
  content: "";
  display: block;
  position: absolute;
  background: rgba(255, 255, 255, 0.5);
  width: 3.125rem;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0.5;
  filter: blur(25px);
  transform: translateX(-90px) skewX(-15deg);
}
.top .feature__content__btn:after {
  content: "";
  display: block;
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  width: 25px;
  height: 100%;
  left: 25px;
  top: 0;
  opacity: 0;
  filter: blur(5px);
  transform: translateX(-90px) skewX(-15deg);
}
.top .feature__content__btn:hover {
  background: var(--very-dark-blue);
  cursor: pointer;
}
.top .feature__content__btn:hover:before {
  transform: translateX(300px) skewX(-15deg);
  opacity: 0.6;
  transition: 0.7s;
}
.top .feature__content__btn:hover:after {
  transform: translateX(300px) skewX(-15deg);
  opacity: 1;
  transition: 0.7s;
}
.top .sidebar {
  background-color: var(--very-dark-blue);
  padding: 1.75rem 1.375rem 0 1.375rem;
  margin-bottom: 4rem;
}
@media (min-width: 48em) {
  .top .sidebar {
    padding: 1.6875rem 1.125rem 0 1.125rem;
    margin-bottom: 0rem;
  }
}
@media (min-width: 64em) {
  .top .sidebar {
    padding: 2.25rem 1.5625rem 0 1.5625rem;
    margin-bottom: 0rem;
  }
}
.top .sidebar__title {
  color: var(--soft-orange);
  margin-bottom: 2.625rem;
}
@media (min-width: 48em) {
  .top .sidebar__title {
    margin-bottom: 1.4375rem;
  }
}
@media (min-width: 64em) {
  .top .sidebar__title {
    margin-bottom: 2.25rem;
  }
}
.top .sidebar__article {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.top .sidebar__article:not(:last-child) {
  border-bottom: 1px solid var(--grayish-blue);
  margin-bottom: 2rem;
}
@media (min-width: 48em) {
  .top .sidebar__article:not(:last-child) {
    margin-bottom: 1.6875rem;
  }
}
.top .sidebar__article h3 {
  color: var(--off-white);
  margin-bottom: 0.875rem;
  transition: all 150ms ease-in-out;
}
.top .sidebar__article h3:hover {
  color: var(--soft-orange);
}
@media (min-width: 48em) {
  .top .sidebar__article h3 {
    margin-bottom: 0.75rem;
  }
}
.top .sidebar__article p {
  color: var(--grayish-blue);
  margin-bottom: 2.25rem;
}
@media (min-width: 48em) {
  .top .sidebar__article p {
    margin-bottom: 1.875rem;
  }
}

.articles {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(1fr);
  gap: 2.125rem;
  margin-bottom: 5.0625rem;
}
@media (min-width: 48em) {
  .articles {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    gap: 2.8125rem;
    margin-bottom: 8.0625rem;
  }
}
.articles .article {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr;
  gap: 1.5625rem;
}
@media (min-width: 48em) {
  .articles .article {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
  }
}
@media (min-width: 64em) {
  .articles .article {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr;
    gap: 1.5625rem;
  }
}
.articles .article__content {
  display: flex;
  flex-direction: column;
  gap: 0.5625rem;
  justify-content: center;
}
@media (min-width: 26.5625em) {
  .articles .article__content {
    gap: 1.25rem;
    justify-content: center;
  }
}
@media (min-width: 48em) {
  .articles .article__content {
    gap: 1.0625rem;
    justify-content: space-between;
    padding-right: 0.625rem;
  }
}
@media (min-width: 64em) {
  .articles .article__content {
    gap: 1.3125rem;
    justify-content: flex-start;
    padding-right: 0;
  }
}
.articles .article__content span {
  color: var(--grayish-blue);
  font-size: var(--fs-lg);
  font-weight: 700;
}
.articles .article__content h4 {
  color: var(--very-dark-blue);
  transition: color 200ms ease-in-out;
}
.articles .article__content h4:hover {
  color: var(--soft-red);
}
.articles .article__content p {
  color: var(--dark-grayish-blue);
}